import React, { Component, Suspense } from "react";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import Modal from "react-modal";
import { GridContacts } from "./common/contact-links";
import styled from "styled-components";

const ContactUs = React.lazy(() => import("./contactUs"));
const Services = React.lazy(() => import("./services"));
const Showroom = React.lazy(() => import("./showroom"));

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    transform: "translate(-50%, -50%)",
    backgroundColor: "#614051",
    border: "5px solid #614051",
    borderRadius: "10px",
    maxWidth: "80%",
    color: "white",
  },
};

const CloseButton = styled.span`
  position: absolute;
  right: 17px;
  top: 5px;
  cursor: pointer;
  font-size: 30px;
  color: white;
  text-align: center;
`;

class Home extends Component {
  state = { modalIsOpen: false, subtitle: "" };

  openModal = () => {
    this.setState({ modalIsOpen: true });
  };

  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };

  render() {
    const { modalIsOpen } = this.state;

    return (
      <Suspense
        fallback={
          <LoadingContainer>
            <LoadingSpinner></LoadingSpinner>
          </LoadingContainer>
        }
      >
        <Services
          theme={this.props.theme.services}
          handleOpenModal={this.openModal}
        />

        <Modal
          isOpen={modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Modal"
        >
          <CloseButton onClick={this.closeModal}>
            <i className="fas fa-times"></i>
          </CloseButton>
          <h2
            style={{
              textAlign: "center",
              marginBottom: "40px",
              marginRight: "25px",
            }}
          >
            Contacte-nos para preços, orçamentos ou outras informações!
          </h2>
          {GridContacts()}
        </Modal>

        <Showroom
          theme={this.props.theme.showroom}
          history={this.props.history}
        />
        <ContactUs theme={this.props.theme.contactUs} />
      </Suspense>
    );
  }
}

export default Home;
