import React, { Component, Suspense } from "react";
import _ from "lodash";
import { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "../common/loading";
import { beUrl } from "../../config";
import {
  Container,
  PaginationContainer,
  Grid,
  FiltersContainer,
} from "./styled-components";

const CarListItem = React.lazy(() => import("../car-list-item"));
const Pagination = React.lazy(() => import("../pagination"));
const Filter = React.lazy(() => import("../filter"));

class CarList extends Component {
  state = {};

  constructor() {
    super();
    this.state.carsPerPage = 12;
    this.state.selectedPage = 1;
    this.state.visibleCars = [];
    this.state.selectedBrands = [];
    this.state.showSold = true;
    this.state.expanded = window.innerWidth > 1024;

    this.handleClickBrand = this.handleClickBrand.bind(this);
    this.handleClickAvailable = this.handleClickAvailable.bind(this);
    this.handleDisableAvailableFilter =
      this.handleDisableAvailableFilter.bind(this);
    this.handleToggleVisibility = this.handleToggleVisibility.bind(this);
  }

  async componentDidMount() {
    let { data: cars } = await axios.get(`${beUrl}/cars`);

    cars = _.sortBy(cars, ["sold", "attributes.brand.value"]);

    this.setState({ cars });
  }

  ApplyFilters() {
    const { selectedBrands, showSold, cars } = this.state;

    let filtered = cars;

    filtered =
      selectedBrands.length === 0
        ? cars
        : filtered.filter((car) =>
            selectedBrands.includes(car.attributes.brand.value)
          );

    filtered = showSold ? filtered : filtered.filter((car) => !car.sold);

    return filtered;
  }

  GetVisibleCars(selectedPage) {
    const carsPerPage = this.state.carsPerPage;
    let filteredCars = this.ApplyFilters();

    const i = (selectedPage - 1) * carsPerPage;
    const f = i + carsPerPage;

    return filteredCars.slice(i, f);
  }

  handleSelectPage = (n) => {
    this.setState({ selectedPage: n });
  };

  handleClickBrand(brand) {
    if (brand === "all") {
      this.setState({ selectedBrands: [] });
      return;
    }

    const { selectedBrands } = this.state;
    let brands = [...selectedBrands];

    if (!selectedBrands.includes(brand)) {
      brands.push(brand);
    } else {
      brands = brands.filter((b) => b !== brand);
    }

    this.setState({ selectedBrands: brands, selectedPage: 1 });
  }

  handleDisableAvailableFilter() {
    this.setState({ showSold: true, selectedPage: 1 });
  }

  handleClickAvailable() {
    this.setState({ showSold: false, selectedPage: 1 });
  }

  handleToggleVisibility() {
    const vis = !this.state.expanded;
    this.setState({ expanded: vis });
  }

  render() {
    const {
      carsPerPage,
      selectedPage,
      selectedBrands,
      showSold,
      cars,
      expanded,
    } = this.state;

    if (cars === undefined) {
      return (
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      );
    } else {
      const filteredCars = this.ApplyFilters();
      const nPages = Math.ceil(filteredCars.length / carsPerPage);
      const selectedPg = selectedPage > nPages ? nPages : selectedPage;

      return (
        <Suspense
          fallback={
            <LoadingContainer>
              <LoadingSpinner></LoadingSpinner>
            </LoadingContainer>
          }
        >
          <ThemeProvider theme={this.props.theme}>
            <Container>
              <FiltersContainer>
                <Filter
                  theme={this.props.theme.filters}
                  onDisableAvailableFilter={this.handleDisableAvailableFilter}
                  onClickAvailable={this.handleClickAvailable}
                  onClickBrand={this.handleClickBrand}
                  selectedBrands={selectedBrands}
                  showSold={showSold}
                  toggleVisibility={this.handleToggleVisibility}
                  expanded={expanded}
                />
              </FiltersContainer>

              <Grid>
                {this.GetVisibleCars(selectedPg).map((car) => (
                  <CarListItem
                    theme={this.props.theme}
                    key={car.id}
                    car={car}
                  />
                ))}
              </Grid>

              <PaginationContainer>
                <Pagination
                  theme={this.props.theme.pagination}
                  numberOfPages={nPages}
                  selectedPage={selectedPg}
                  onSelectPage={this.handleSelectPage}
                />
              </PaginationContainer>
            </Container>
          </ThemeProvider>
        </Suspense>
      );
    }
  }
}

export default CarList;
