import { useState, useEffect } from "react";
import { v4 as uuidv4 } from "uuid";

const DynamicAttributes = ({ car, saveAttributes }) => {
  const [attributes, setAttributes] = useState(car.attributes);

  useEffect(() => {
    saveAttributes(attributes);
  }, [attributes]);

  const handleChange = ({ currentTarget: input }) => {
    const att = { ...attributes };
    att[input.getAttribute("data-key")][input.name] = input.value;
    setAttributes(att);
  };

  const handleAdd = (e) => {
    e.preventDefault();
    const att = { ...attributes };
    att[uuidv4()] = {
      value: "",
      description: "",
    };
    setAttributes(att);
  };

  const handleDelete = (e) => {
    e.preventDefault();
    const att = { ...attributes };
    delete att[e.currentTarget.getAttribute("data-key")];
    setAttributes(att);
  };

  const isUUID = (key) =>
    /^[0-9A-Z]{8}-[0-9A-Z]{4}-[0-9A-Z]{4}-[0-9A-Z]{4}-[0-9A-Z]{12}$/i.test(key);

  return (
    <div>
      <h3>Detalhes</h3>
      {Object.keys(attributes).map((key) => {
        return isUUID(key) ? (
          <div className="form-group" key={key}>
            <div className="form-row">
              <div className="col">
                <label>Descrição</label>

                <div className="input-group mb-3">
                  <div className="input-group-prepend">
                    <button
                      className="btn btn-danger"
                      type="button"
                      data-key={key}
                      onClick={handleDelete}
                    >
                      Eliminar
                    </button>
                  </div>
                  <input
                    type="text"
                    data-key={key}
                    value={attributes[key].description}
                    name="description"
                    onChange={handleChange}
                    className="form-control"
                    aria-describedby="basic-addon1"
                  />
                </div>
              </div>
              <div className="col">
                <label>Valor</label>
                <input
                  type="text"
                  data-key={key}
                  value={attributes[key].value}
                  name="value"
                  onChange={handleChange}
                  className="form-control"
                />
              </div>
            </div>
          </div>
        ) : (
          ""
        );
      })}
      <button className="btn btn-light mb-2" onClick={handleAdd}>
        <i className="fas fa-plus"></i>
      </button>
    </div>
  );
};

export default DynamicAttributes;
