import React from "react";
import { useState } from "react";
import { useSwipeable } from "react-swipeable";
import {
  Ul,
  Li,
  Arrow,
  Thumbnail,
  Container,
  ThumbnailBox,
} from "./carousel-styled-components";

const getPhotos = (car, selected) => {
  const items = [];

  for (let i = 1; i <= car.photos; i++) {
    if (i === selected) {
      items.push(
        <Li key={`${car.id}/${i}`}>
          <img
            src={process.env.PUBLIC_URL + `/img/${car.id}/${i}.webp`}
            alt={`Foto ${i} da viatura`}
            draggable={false}
          />
        </Li>
      );
    }
  }

  return items;
};

const setThumbnailBoxScroll = (selectedIndex) => {
  const target = document.getElementById("selectedThumb");
  if (target !== null && target.parentNode !== undefined) {
    const style = getComputedStyle(target);
    const spacing = parseInt(style.marginRight);
    target.parentNode.scrollLeft =
      (target.clientWidth + spacing) * (selectedIndex - 1);
  }
};

const Carousel = (props) => {
  const [selectedIndex, setSelectedIndex] = useState(1);
  const { car } = props;

  const next = () => {
    const newIndex = selectedIndex < car.photos ? selectedIndex + 1 : 1;
    setSelectedIndex(newIndex);
    setThumbnailBoxScroll(newIndex);
  };

  const previous = () => {
    const newIndex = selectedIndex > 1 ? selectedIndex - 1 : car.photos;
    setSelectedIndex(newIndex);
    setThumbnailBoxScroll(newIndex);
  };

  const selectPhoto = (n) => {
    setSelectedIndex(n);
  };

  const getThumbnails = (car, selected) => {
    const items = [];

    for (let i = 1; i <= car.photos; i++) {
      items.push(
        <Thumbnail
          key={`${car.id}/${i}`}
          className={selected === i ? "active" : "transparency"}
          id={selected === i ? "selectedThumb" : ""}
        >
          <img
            src={process.env.PUBLIC_URL + `/img/${car.id}/${i}.webp`}
            alt={`Foto ${i} da viatura`}
            onClick={() => selectPhoto(i)}
            draggable={false}
          />
        </Thumbnail>
      );
    }

    return items;
  };

  const handlers = useSwipeable({
    onSwipedLeft: () => next(),
    onSwipedRight: () => previous(),
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
  });

  return (
    <>
      <Container>
        <Arrow {...handlers} onClick={previous} className="left">
          <i className="fas fa-chevron-left"></i>
        </Arrow>

        <Arrow {...handlers} onClick={next} className="right">
          <i className="fas fa-chevron-right"></i>
        </Arrow>

        <Ul {...handlers}>{getPhotos(car, selectedIndex)}</Ul>
      </Container>

      <ThumbnailBox>{getThumbnails(car, selectedIndex)}</ThumbnailBox>
    </>
  );
};

export default Carousel;
