import React, { useState, useEffect, Suspense } from "react";
import { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "../common/loading";
import Helmet from "react-helmet";
import { toast } from "../common/helper";
import { beUrl } from "../../config";
import { Link } from "react-router-dom";
import { removeStyle } from "../../styles/links";
import {
  FullscreenToggle,
  BackToList,
  H1,
  Error404,
  DetailsList,
} from "./styled-components";
import {
  GetCarSpecsTable,
  getBadge,
  structuredJSON,
} from "./car-details-funtions";
import Carousel from "./carousel";

const ContactForm = React.lazy(() => import("../form/contact-form"));

function CarDetails(props) {
  const [fullscreen, setFullscreen] = useState(false);
  const [car, setCar] = useState(undefined);

  useEffect(() => {
    async function fetchData() {
      try {
        let { data: c } = await axios.get(
          `${beUrl}/cars/${props.match.params.id}`
        );

        if (!c) {
          c = -1;
        }

        setCar(c);

        if (c.sold) {
          toast.error("⚠️ Esta viatura já foi vendida 😢");
        }
      } catch {
        setCar(-1);
      }
    }
    fetchData();
  }, [props.match.params.id]);

  const { id } = props.match.params;

  const toggleFullscreen = () => {
    setFullscreen((prev) => !prev);
  };

  if (!car) {
    return (
      <LoadingContainer>
        <LoadingSpinner></LoadingSpinner>
      </LoadingContainer>
    );
  }

  if (car === -1) {
    return (
      <ThemeProvider theme={props.theme}>
        <Error404>
          <span>Erro:</span> Viatura não existe: id {id}
        </Error404>
      </ThemeProvider>
    );
  }

  return (
    <Suspense
      fallback={
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      }
    >
      <ThemeProvider theme={props.theme}>
        <Helmet>
          <script type="application/ld+json">{structuredJSON(car)}</script>
          <meta charSet="utf-8" />
          <title>{car.title}</title>
          <link rel="canonical" href="https://vinculobjetivo.pt/carros" />
        </Helmet>

        <FullscreenToggle onClick={toggleFullscreen}>
          <i className={fullscreen ? "fas fa-compress" : "fas fa-expand"}></i>
        </FullscreenToggle>
        {!fullscreen ? (
          <div>
            <Link to="/carros" style={{ ...removeStyle }}>
              <BackToList>
                <i className="fas fa-arrow-left"></i> Catálogo
              </BackToList>
            </Link>
            <div className="row ml-lg-5 mr-lg-5 ml-sm-2 mr-sm-2 ml-1 mr-1">
              <div className="col-12 col-lg-9">
                <Carousel car={car} />
              </div>
              <div className="col-12 col-lg-3 ">
                <H1>{car.title}</H1>
                {getBadge(car)}
                <DetailsList>{GetCarSpecsTable(car)}</DetailsList>
              </div>
            </div>
            <ContactForm theme={props.theme} subject={car.title}></ContactForm>
          </div>
        ) : (
          <Carousel car={car} />
        )}
      </ThemeProvider>
    </Suspense>
  );
}

export default CarDetails;
