import React, { useEffect, useState } from "react";
import axios from "axios";
import { beUrl } from "../../config";
import { formatKms, formatPrice, userLogged } from "../common/helper";
import { Link, useHistory } from "react-router-dom";

const Inventory = (props) => {
  const [cars, setCars] = useState(undefined);
  const history = useHistory();

  useEffect(() => {
    if (!userLogged()) {
      history.push("/admin/login");
    }
  }, []);

  useEffect(() => {
    async function fetchData() {
      const { data } = await axios.get(`${beUrl}/cars`);
      setCars(data);
    }
    fetchData();
  }, []);

  const getButton = (txt, handleClick) => {
    return (
      <button
        className="btn m-2"
        onClick={handleClick}
        style={{ color: "#307DF6", border: "solid #307DF6 1px" }}
      >
        {txt}
      </button>
    );
  };

  return cars ? (
    <>
      {getButton("Logout", () => {
        localStorage.removeItem("token");
        history.push("/admin/login");
      })}
      {getButton("Adicionar", () => history.push("/admin/new"))}

      <table className="table table-hover">
        <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col">Título</th>
            <th scope="col">Data Matrícula</th>
            <th scope="col">KMs</th>
            <th scope="col">Preço</th>
            <th scope="col">Destacado</th>
            <th scope="col">Editar</th>
          </tr>
        </thead>
        <tbody>
          {cars.map((car) => {
            return (
              <tr key={car.id}>
                <td>{car.id}</td>
                <td>{car.title}</td>
                <td>{`${car.attributes.month.value} / ${car.attributes.year.value}`}</td>
                <td>{formatKms(car.attributes.kms.value)}</td>
                <td>{formatPrice(car.price)}</td>
                <td>
                  {car.showroom ? <i className="far fa-check-circle"></i> : ""}
                </td>
                <td>
                  <Link to={`/admin/edit/${car.id}`}>
                    <i className="far fa-edit"></i>
                  </Link>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  ) : (
    <h1>Loading...</h1>
  );
};

export default Inventory;
